import {
  LOGIN_FAILURE,
  LOGIN_PASS
} from '../mutation-types'

// initial state
const state = {
  auth_id: null,
  email:'',
  password:''

}



// mutations
const mutations = {
  USER_UPDATEED (sate,user){
    state.email=user.email
    state.password=user.password
    console.log(state.email)

  },
  [LOGIN_PASS] (state, uid) {
    state.auth_id = uid
    console.log(state.auth_id)
    window.router.go({ name: 'demo'})
  },

  [LOGIN_FAILURE] (state) {
     state.auth_id = null
     console.log('LOGIN_FAILURE')
  }
}

export default {
  state,
  mutations
}



